.nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 2rem;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    -webkit-transition: 0.3s;
	-o-transition: 0.3s;
	transition: 0.3s;
    
}
.nav_scrolled{
    height:80px;
    background-color: #0a0a0a;
    box-shadow: 0 0 10px rgba(0,0,0,0.1);
    -webkit-transition: 0.3s;
	-o-transition: 0.3s;
	transition: 0.3s;
}
.nav__logo{
    width: 100px;
    height: 100px;
    object-fit: contain;
}
.nav__menu{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 500px;
    margin-right: 20px;
}
.nav__link{
    text-decoration: none;
    color: #000;
    margin-right: 10px;
    font-size: 1.2rem;
    font-weight: 600;
    transition: all 0.2s ease-in-out;
}
.nav__link:hover{
    color: #e50914;
    transition: all 0.2s ease-in-out;
}
.nav__avatar{
    width: 30px;
    height: 30px;
    object-fit: contain;
    cursor: pointer;
}
