.card {
	position: relative;
    min-width: 182px;
    height: auto;
    border-radius: 4px;
    background-color: #fff;
    transition: transform 450ms;
    
}

.card:hover{
	transform:scale(1.2);
	z-index: 1;
	-webkit-box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.55);
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.55);
}
.card_big{ 
	min-width: 300px;
}
.card_big:hover{ 
	transform:scale(1.16);
	z-index: 1;
	-webkit-box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.55);
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.55);
}
.card__image {
	display: block;
	-o-object-fit: cover;
	object-fit: cover;
	width: 100%;
	height: 100%;
}

.card__place-title {
	margin: 25px 15px 31px 21px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: justify;
	-ms-flex-pack: justify;
	justify-content: space-between;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
}

.card__place-name {
	margin: 0;
	-o-text-overflow: ellipsis;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
	color: #000;
	font-size: 24px;
	font-weight: 900;
	text-align: left;
}

.card__place-favorite {
	background-image: url(../images/heart.svg);
	background-size: contain;
	background-repeat: no-repeat;
	background-color: white;
	min-width: 21px;
	height: 19px;
	-webkit-transition: 0.2s;
	-o-transition: 0.2s;
	transition: 0.2s;
	color: transparent;
	border: none;
}
.card__links{
	margin:0;
}
.card__title{
margin:0;
}
.card__vote-average{
	margin:0;
	color:#46d369;
}
.card__info{
	opacity: 0;
	box-sizing: border-box;
	text-decoration: none;
	background:#181818e5;
	position:absolute;
	bottom:0;	
	width:100%;
	padding: 10px 20px;
	transition:0.3s;
    
}
.card:hover .card__info{
	opacity: 1;
}
.card__info-middle{
	display: flex;
	justify-content: space-between;
	align-items: center;
	width:100%;
	margin:5px 0;
}
.card__play{
	width:30px;
	filter: invert(100%);
}
.card__view-more{
	width:30px;
	padding:10px;
	cursor:pointer;
	filter: invert(100%);
}

.card__place-favorite:hover {
	opacity: 0.5;
	cursor: pointer;
}

.card__place-favorite_active {
	background-image: url(../images/heart-fill.svg);
}

.card__trash {
	position: absolute;
	top: 18px;
	right: 15px;
	opacity: 0;
	background: none;
	border: none;
	transition: 0.3s;
}
.card__trash_visible {
	visibility: visible;
  }
  
.card__trash_hidden {
	visibility: hidden;
  }

.card__trash:hover {
	cursor: pointer;
}

.card:hover .card__trash {
	opacity: 1;
}

.card__place-num {
	width: 22px;
	height: 22px;
	margin: 0;
	font-size: 13px;
    line-height: 1.7;
    text-align: center;
    background-color: #e1516a;
    border-radius: 50px;
    color: #fff;
}

@media screen and (max-width:778px) {
	.card__trash {
		opacity: 1;
	}
}